a {
  color: #3346d1;
}
a:hover {
  color: rgba(51, 70, 209, 0.8);
}
.page-header-alpha .navbar-mainmenu .nav-item a:hover {
  color: #3346d1;
}
.btn-white {
  color: #fff;
  border: 1px solid transparent;
  background-color: #3346d1;
}
.btn-white:hover {
  background-color: rgba(51, 70, 209, 0.8);
  color: #fff;
}
.btn-outline-primary {
  color: #3346d1;
  border-color: currentColor;
}
.btn-outline-primary:hover,
.btn-outline-primary:focus {
  color: rgba(51, 70, 209, 0.5);
  border-color: currentColor;
}
.btn-transp-arrow.btn-primary.active,
.btn-transp-arrow.btn-primary:active,
.btn-transp-arrow.btn-primary:hover {
  background-color: rgba(77, 77, 77, 0.05);
  color: #8b8b8b;
  -webkit-box-shadow: none;
  box-shadow: none;
}
.form-container-transparent.form-container-white .form-desc h4 {
  color: #fff;
}
.form-container-transparent.form-container-white .form-input label {
  color: #fff;
}
.form-control-outline,
.form-control-line {
  border-color: #fff;
}
.form-control-outline:focus,
.form-control-line:focus {
  border-color: #adadad;
}
.page-cover:after {
  content: "";
  background: -webkit-gradient(linear, right top, left top, from(#3346d1), to(darkslateblue));
  background: linear-gradient(to left, #3346d1, darkslateblue);
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  opacity: 0.7;
}
