.about-description {
  padding: 15px;
  top: -130px;
  background: rgba(27, 27, 27, 0.8);
  background: rgba(27, 27, 27, 0.8);
}
.about-description p {
  margin: 0;
}
.about-section .shadow {
  -webkit-box-shadow: 0 0 40px rgba(0, 0, 0, 0.3);
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.3);
}
@media (max-width: 1366px) {
  .about-description {
    top: -90px;
  }
  .section-title .title-bg {
    font-size: 5em;
  }
  .media .icon {
    font-size: 3rem;
  }
  .price-item .price-big {
